.imageContainer {
  /* overflow: hidden; */
  display: block;
  min-width: 0;
  min-height: 0;
}
.imageContainer img {
  display: block;
  object-fit: cover;
  max-width: 100%;
}
.title {
  margin: unset;
}
.videoContainer {
  display: flex;
  aspect-ratio: 16/9;
  padding-bottom: 3vh;
  align-items: center;
  justify-content: start;
  width: 100%;
}
@supports not (aspect-ratio: 16/9) {
  .videoContainer {
    display: flex;
    padding-bottom: 3vh;
    align-items: center;
    justify-content: start;
    width: 100%;
    height: 60%;
  }
}
.text {
  margin-bottom: 5vh;
}

.section {
  scroll-snap-align: start;
  display: grid;
  grid-gap: 2vh;
  grid-template-columns: 1fr;
  overflow: hidden;
  align-items: center;
  width: 50vw;
  min-height: 80vh;
  box-sizing: border-box;
}
.container {
  scroll-snap-type: y mandatory;
  overflow-y: scroll;
  box-sizing: border-box;
  height: 88vh;
  width: 50vw;
}
.contentContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: auto;
  z-index: 0;
  text-align: start;
  line-height: 1.6;
  letter-spacing: 0.1em;
  overflow: hidden;
}
.container4tet {
  position: absolute;
  top: 12vh;
  left: 0px;
  height: 80vh;
  width: 50vw;
  overflow: scroll;
  color: black;
}

@media only screen and (max-width: 600px) {
  .container {
    scroll-snap-type: y mandatory;
    overflow-y: scroll;
    box-sizing: border-box;
    height: 70vh;
    width: 90vw;
  }
  .contentContainer {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: auto;
    z-index: 0;
    text-align: start;
    line-height: 1.6;
    letter-spacing: 0.1em;
    overflow: hidden;
  }
  .container4tet {
    position: absolute;
    top: 15vh;
    left: 0px;
    height: 78vh;
    width: 100vw;
    overflow: scroll;
    color: black;
  }
  .section {
    scroll-snap-align: start;
    display: grid;
    grid-gap: 2vh;
    grid-template-columns: 1fr;
    overflow: hidden;
    align-items: center;
    width: 90vw;
    min-height: 80vh;
  }
}
